<template>
    <div>
      <el-tree
        :data="data"
        show-checkbox
        default-expand-all
        node-key="id"
        ref="tree"
        highlight-current
        :props="defaultProps">
      </el-tree>
      <el-button @click.native.prevent="getCheckedKeys">通过 key 获取</el-button>
      <error-page></error-page>
    </div>
</template>

<script>
  import errorPage from '../errorPage/errorPage401'
    export default {
        data() {
            return {
              data: [{
                id: 1,
                label: '一级 1',
                children: [{
                  id: 4,
                  label: '二级 1-1',
                  children: [{
                    id: 9,
                    label: '三级 1-1-1'
                  }, {
                    id: 10,
                    label: '三级 1-1-2'
                  }]
                }]
              }, {
                id: 2,
                label: '一级 2',
                children: [{
                  id: 5,
                  label: '二级 2-1'
                }, {
                  id: 6,
                  label: '二级 2-2'
                }]
              }, {
                id: 3,
                label: '一级 3',
                children: [{
                  id: 7,
                  label: '二级 3-1'
                }, {
                  id: 8,
                  label: '二级 3-2'
                }]
              }],
              defaultProps: {
                children: 'children',
                label: 'label'
              }
            }
        },
        methods:{
          getCheckedKeys() {
            console.log(this.$refs.tree.getCheckedKeys());
          },
        },
        components: {
          errorPage
        }
    }
</script>

<style scoped>

</style>



